.userCtn--img {
  width: 150px;
  height: 150px;
  border-radius: 196px;
}

.userCtn .btn-y {
  font-size: 0.7em;
  background: rgba(23, 23, 23, 0.7);
  color: #fff;
  cursor: pointer;
  margin-top: 1em;
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  border-radius: 500px;
  padding: 13px;
  letter-spacing: 2px;
  line-height: 1.6;
}

.userCtnfooterLinks {
  margin: 40px;
  color: gray;
}

.userCtn a {
  text-decoration: none;
  font-size: 12px;
  display: inline-block;
  color: currentColor;
}

.nav {
  width: 25%;
  background: #1C1D1C;
  height: 100%;
  overflow: auto;
  float: left;
  z-index: 20;
  position: fixed;
  left: 0px;
  min-height: 600px;
  opacity: 0.9;
}

.nav__img {
  margin: 70px auto 85px auto;
  display: block;
  width: 80px;
  height: auto;
}

