/*hhh*/
.searchContainer {
  position: relative;
  margin-left: 260px;
  color: white;
  background-color: #282828;
  padding: 1.5em 2em;
}

.searchContainer__searchTitle {
  font-size: 14px;
  letter-spacing: 1px;
  margin-bottom: .5em;
}

.searchContainer__searchInput {
  caret-color: #1db954;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  background-color: transparent;
  border: none;
  color: white;
  font-weight: 700;
  font-size: 3rem;
  line-height: 1.6;
}

.searchContainer__searchInput:focus {
  outline: 0px;
}

.container {
  background: -webkit-gradient(linear, left top, left bottom, from(#178120), color-stop(85%, #0c0402));
  background: -webkit-linear-gradient(#178120, #0c0402 85%);
  background: -o-linear-gradient(#178120, #0c0402 85%);
  background: linear-gradient(#178120, #0c0402 85%);
  background: cover;
  position: absolute;
  width: 100%;
  height: 100%;
}

.y-web-nav {
  width: 220px;
  background: rgba(124, 78, 64, 0.5);
  height: 100%;
  overflow: auto;
  float: left;
  z-index: 20;
  position: fixed;
  left: 0px;
  min-height: 600px;
  opacity: 0.9;
}

.y-footer {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0px;
  border: 0px;
  padding: 0px;
  background-color: #181818;
  position: fixed;
  width: 100%;
  bottom: 0;
  z-index: 101;
}

.y-footer .player-controls {
  -ms-flex-align: center;
      align-items: center;
  height: 90px;
}

.y-footer .player-controls__buttons {
  margin-bottom: 12px;
  cursor: default;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  margin: 2% 1%;
}

.ycontain {
  background: -webkit-radial-gradient(#2E1D86, #04021F);
}

.ycontain__ycontext {
  text-align: center;
  color: #fff;
}

.ycontain__ycontext--yimg {
  margin-top: 5%;
  border-radius: 5%;
  width: 45%;
  text-decoration: none;
}

.ycontain__ycontext--ybtn {
  color: #fff;
  background-color: #1db954;
  padding: 11px 44px 12px;
  margin: 3%;
}


